import { useI18next } from "gatsby-plugin-react-i18next";
import { Edition, events, EventTypes } from "../data";

import edition2022 from "../../data/2022.json";
import edition2023 from "../../data/2023.json";
import edition2024 from "../../data/2024.json";

const useEditionData = (edition: Edition) => {
  switch (edition) {
    case 2022:
      return edition2022;
    case 2023:
      return edition2023;
    case 2024:
      return edition2024;
    default:
      throw `unknown edition ${edition}`;
  }
};

const useDates = (edition: Edition, event: EventTypes) => {
  return useEditionData(edition)[event].map(
    (e: string) => new Date(`${e} ${edition}`),
  );
};

export const getCompetitionLink = (edition: Edition) => {
  return useEditionData(edition).competition;
};

export const getRecordingsLink = (edition: Edition) => {
  return useEditionData(edition).recordings;
};

export const getResourcesLink = (edition: Edition) => {
  return useEditionData(edition).resources;
};

export const getInterval = (edition: Edition) => {
  const dates = useDates(edition, "hackathonRegistration");
  dates[1].setDate(dates[1].getDate() + 1);
  return dates;
};

export const getDate = (edition: Edition, event: any) => {
  if (useEditionData(edition)[event as EventTypes][0] == "TBA") {
    return "TBA";
  }

  const eventDates = useDates(edition, event);
  const locales = useI18next().language == "ro" ? "ro-RO" : "en-GB";
  const options = { day: "numeric", month: "long" } as const;

  return (
    eventDates[0].toLocaleDateString(locales, options) +
    (eventDates.length > 1
      ? " - " + eventDates[1].toLocaleDateString(locales, options)
      : "")
  );
};

export const getWorkshopDate = (edition: Edition, days: number) => {
  const date = useDates(edition, "workshops")[0];
  date.setDate(date.getDate() + days);
  const locales = useI18next().language == "ro" ? "ro-RO" : "en-GB";
  const options = { day: "numeric", month: "long" } as const;

  return date.toLocaleDateString(locales, options);
};

export const getSortedEvents = (edition: Edition) => {
  const sortedEvents = [...events].map((event) => {
    return {
      title: event,
      date:
        useEditionData(edition)[event as EventTypes][0] == "TBA"
          ? Infinity
          : useDates(edition, event)[0],
    };
  });

  return sortedEvents.sort((a, b) => {
    if (a.date < b.date) {
      return -1;
    }

    if (a.date > b.date) {
      return 1;
    }

    return 0;
  });
};

export const getAnnouncement = (edition: Edition) => {
  const today = new Date();
  const hackathonRegistration = useDates(edition, "hackathonRegistration");
  const date = hackathonRegistration[1];
  date.setDate(date.getDate() - 3);
  const workshopDates = useDates(edition, "workshops");
  const hackathonDates = useDates(edition, "hackathon");

  return today < hackathonRegistration[0]
    ? "pre-registrations"
    : today < date
      ? "registrations"
      : today < hackathonRegistration[1]
        ? "post-registrations"
        : today < workshopDates[0]
          ? "pre-workshops"
          : today < workshopDates[1]
            ? "workshops"
            : today < hackathonDates[0]
              ? "pre-hackathon"
              : today < hackathonDates[0]
                ? "hackathon"
                : "post-hackathon";
};
