import React from "react";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { Button } from "react-bootstrap";
import { getCompetitionLink, getInterval } from "../../../hooks/useEditionData";
import { currentEdition } from "../../../data";
import { PeriodType } from "../types";
import * as styles from "./buttons.module.css";

const preRegistration: PeriodType = {
  props: {
    to: "/schedule",
  },
  text: "navbar.schedule",
};
const Registration: PeriodType = {
  props: {
    target: "_blank",
    rel: "noreferrer",
    as: "a",
    href: "https://forms.zohopublic.eu/nitrolanguageprocessing/form/nscriereNitroHackathon/formperma/pHkcA9Uoak67yDFN8favZvt4aN1PTQSw1mkKYIsnATc",
  },
  text: "register",
};
const postRegistration: PeriodType = {
  props: {
    as: "a",
    href: getCompetitionLink(currentEdition),
  },
  text: "navbar.competition",
};

type RegisterButtonProps = {
  className?: string;
  inNavbar?: boolean;
};

const RegisterButton: React.FC<RegisterButtonProps> = ({ inNavbar }) => {
  const { t } = useTranslation();
  const registrationDates = getInterval(currentEdition);
  const today = new Date();
  const period =
    today < registrationDates[0]
      ? preRegistration
      : today <= registrationDates[1]
        ? Registration
        : postRegistration;

  return (
    !(period == preRegistration && inNavbar) && (
      <Button
        {...period.props}
        className={inNavbar ? styles.buttonLink : styles.button}
      >
        {t(period.text)}
      </Button>
    )
  );
};

export default RegisterButton;
